.dropzone {
    margin: 0 auto;
    max-width: 1024px;
    border: 2px dashed #9fa9a9;
    background-color: #e0ffff;
    color: #6a6b80;
    padding: 20px;
    font-weight: bold;
    font-size: 20px;
    font-family: Gadget, sans-serif;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-transition: border 0.5s linear, -webkit-box-shadow 0.5s linear;
    transition: border 0.5s linear, -webkit-box-shadow 0.5s linear;
    -o-transition: box-shadow 0.5s linear, border 0.5s linear;
    transition: box-shadow 0.5s linear, border 0.5s linear;
    transition: box-shadow 0.5s linear, border 0.5s linear, -webkit-box-shadow 0.5s linear;
}

.dropzone.active {
    -webkit-box-shadow: 0 0 20px 3px #000;
            box-shadow: 0 0 20px 3px #000;
    border: 2px solid #80818a;
}


